//For custom style
//main

/* custom scrollbar width */
::-webkit-scrollbar {
  width: 7px;
}
/* Track */
::-webkit-scrollbar-track {
  background: #181818; 
}
/* Handle */
::-webkit-scrollbar-thumb {
  background:  rgba(255, 255, 255, 0.2);
  border-radius: 25px; 
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.4); 
}
.invisibleScroll{
  &::-webkit-scrollbar{
    width: 0;
  }
}
html{
  height:100%;
}
body{
  min-height: 100%;
  display: flex;
}
#root{
  display: flex;
  width:100%;
  padding: 0;
  min-height: 900px;
}
svg{
  fill:inherit;
  path{
    fill:inherit;
  }
}
.logoStyle{
  margin:0;
  padding:35px 21px;
  font-weight: 700;
  font-size: 13px;
  line-height: 15px;
  letter-spacing: -0.05em;
  background: linear-gradient(270deg, #FFD25F 0.13%, #FF5C01 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}